.viber__switch_container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 38px;
  padding: 0 20px 10px;
  border-bottom: 1.75px solid #F4F5F5;
}

.viber__image {
  background: #9B51E0;
  border-radius: 50%;
  color: black;
  position: relative;
  height: 40px;
  width: 40px;
}

.viber__icon {
  fill: currentColor;
  height: 50%;
  left: 50%;
  position: absolute;
  stroke: currentColor;
  stroke-width: 0;
  top: 50%;
  width: 50%;
  transform: translate(-50%, -50%);
}

.viber__text {
  padding-right: 60px;
  padding-left: 10px;
}

.viber__switch {
  display: flex;
}
