.input {
    display: flex;
    cursor: text;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    border-bottom: 1px solid #D9DADC;
    transition: 0.3s;
    position: relative;
}

.input_status_active {
    box-shadow: 0 0 11px 0 black;
}
.input_status_error {
    border: 2px solid var(--colors-interface-error);
}
.input_status_disabled{
    cursor: not-allowed;
}

.input_type_disabled {
    cursor: not-allowed;
}
.input__icon {
    cursor: text;
    line-height: 0;
}
.input__icon-action{
    cursor: pointer;
}
.input__icon-disabled{
    cursor: not-allowed;
}
.input__placeholder {
    display: block;
    position: absolute;
    cursor: text;
    top: 18px;
    text-align: left;
    color: #77787A;
    transition: 0.3s;
}
.input__placeholder-focused{
    top: 8px;
}
.input__placeholder-filled{
    top: 8px;
}

.input__textfield {
    display: block;
    cursor: text;
    background-color: transparent;
    border: none;
    width: 100%;
    color: #202A3C;
    margin-top: 16px !important;
}

.input__textfield:focus {
    outline: none;
}

.input__container {
    width: 100%;
    text-align: left;
    margin-bottom: 50px;
}

.input__container:first-child {
    margin-top: 30px;
}

.input__container:nth-child(4){
    margin-bottom: 10px;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
}

.input__placeholder-disabled,
.input__textfield-disabled {
    cursor: not-allowed;
}

.input__error{
    margin-left: 35px;
    margin-top: 2px;
    color: var(--colors-interface-error);
}

.input_size_default {
    max-width: 1042px;
    min-height: 56px;
    margin: 0 auto;
}

.input_size_small {
    width: 304px;
    min-height: 48px;
    padding-left: calc(var(--offset-base-variable)  * 10);
    padding-right: calc(var(--offset-base-variable)  * 10);
}
